import { slidesURL, categoriesURL } from "./consts";

const apiCall = {
  getSlides: async () => {
    if (navigator.onLine) {
      const slides = await fetch(
        "https://cors-anywhere.herokuapp.com/" + slidesURL
      )
        .then(res => res.json())
        .then(res => {
          return res.data;
        })
        .catch(err => console.error(err));
      localStorage.setItem("slides", JSON.stringify(slides));
      return slides;
    } else {
      return JSON.parse(localStorage.getItem("slides"));
    }
  },
  getCategories: async () => {
    if (navigator.onLine) {
      const categories = await fetch(
        "https://cors-anywhere.herokuapp.com/" + categoriesURL
      )
        .then(res => res.json())
        .then(res => res.data)
        .catch(err => console.error(err));
      localStorage.setItem("categories", JSON.stringify(categories));
      return categories;
    } else {
      return JSON.parse(localStorage.getItem("categories"));
    }
  }
};

export default apiCall;
