import React, { Component } from "react";

import Slider from "./Slider/index";
import Tags from "./Tags/index";
import Search from "./Search/index";
import Nearby from "./Nearby/index";
import Categories from "./Categories/index";

import LoadingOverlay from "../Loading";

import apiCall from "../../apiCall";

function delayUnmounting(Component) {
  return class extends React.Component {
    state = {
      shouldRender: this.props.isMounted
    };

    componentDidUpdate(prevProps) {
      if (prevProps.isMounted && !this.props.isMounted) {
        this.setState({ class: "fadeOut" });
        setTimeout(
          () => this.setState({ shouldRender: false }),
          this.props.delayTime
        );
      } else if (!prevProps.isMounted && this.props.isMounted) {
        this.setState({ shouldRender: true });
      }
    }

    render() {
      return this.state.shouldRender ? <Component {...this.props} /> : null;
    }
  };
}

const DelayedComponent = delayUnmounting(LoadingOverlay);

export default class HomePage extends Component {
  state = {
    slides: [],
    categories: [],
    loading: true
  };
  async componentWillMount() {
    var slides = await apiCall.getSlides();
    var categories = await apiCall.getCategories();
    if (categories) {
      this.setState({
        slides: slides,
        categories: categories,
        loading: false
      });
    } else {
      alert("اشکال در اتصال");
    }
  }
  render() {
    return (
      <div className="container">
        {!this.state.loading ? (
          <div>
            {/* <AppNav /> */}
            <Slider slides={this.state.slides} />
            <Search />
            <Tags />
            <Nearby />
            <Categories categories={this.state.categories} />
          </div>
        ) : (
          ""
        )}
        <DelayedComponent
          delayTime={500}
          isMounted={this.state.loading}
          className={!this.state.loading && "fadeOut"}
        />
      </div>
    );
  }
}
