import React, { Component } from "react";

import SlideWrapper from "./slideWrapper";

import apiCall from "../../../apiCall";

import Loading from "../../../assets/ajax-loader.gif";

export default class Slider extends Component {
  state = {
    slides: [],
    loading: true
  };

  render() {
    const slidesRender =
      this.props.slides &&
      this.props.slides.map((slide, key) => {
        return (
          <SlideWrapper
            imageURL={slide.photo_url}
            title={slide.title}
            url=""
            key={key}
          />
        );
      });
    return (
      <div className="sliderWrapper">
        <div className="sliderInner">{slidesRender}</div>
      </div>
    );
  }
}
